<template>
  <div>
    <div id="TOP">
      <h1>商品分类</h1>
      <el-button type="primary"
                 @click="clickADD">添加分类</el-button>
    </div>
    <!-- 模态 -->
    <el-dialog title="提示"
               :visible.sync="centerDialogVisible"
               width="30%"
               center>
      <el-select v-model="value"
                 clearable
                 placeholder="请选择">
        <el-option v-for="(item,index) in options"
                   :key="index"
                   :label="item.label"
                   :value="item">
        </el-option>
      </el-select>
      <el-switch v-model="state"
                 active-color="#13ce66"
                 inactive-color="#ff4949">
      </el-switch>

      <span slot="footer"
            class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="clickADD_OK">确 定</el-button>
      </span>
    </el-dialog>

    <el-table :data="datalist">
      <el-table-column label="分类名称">
        <template slot-scope="scope">
          <span v-show="!scope.row.isedit">{{ scope.row.cateName }}</span>
          <el-input v-show="scope.row.isedit"
                    v-model="scope.row.cateName" />
        </template>
      </el-table-column>
      <el-table-column label="是否启用">
        <template slot-scope="scope">
          <el-switch :disabled="!scope.row.isedit"
                     active-color="#13ce66"
                     inactive-color="#ff4949"
                     v-model="scope.row.state"></el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini"
                     @click="clickEdit(scope)">{{ scope.row.isedit ? '完成': '编辑' }}</el-button>
          <el-button @click.native.prevent="deleteRow(scope.$index, datalist)"
                     type="danger"
                     size="mini">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="curpage"
                   :page-sizes="pagesizes"
                   :page-size="pagesize"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="total"></el-pagination>
  </div>

</template>

<script>
//添加商品分类addcate
import { catelist, editcate, delcate, categories, addcate } from '@/API/apis'//添加商品分类

export default {
  data () {
    return {
      datalist: [],
      curpage: 1,
      pagesize: 6,
      pagesizes: [6, 12, 18, 24],
      total: 0,
      centerDialogVisible: false,
      state: true,
      options: [],
      value: ""
    };
  },
  created () {
    this.List()
  },
  methods: {
    clickEdit (scope) {
      if (scope.row.isedit) {
        //发送请求，修改数据
        editcate(scope.row.id, scope.row.cateName, scope.row.state).then(res => {
          console.log(111);
          if (res.data.code == 0) {
            this.$message({
              message: "分类修改成功",
              type: "success"
            });
            scope.row.isedit = false;
          }
        });
      } else {
        scope.row.isedit = true;
      }
    },
    // 删除
    deleteRow (index) {
      // rows.splice(index, 1);
      this.$confirm('此操作将永久删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delcate(this.datalist[index].id, 1).then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            this.List()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    handleSizeChange (newpagesize) {
      this.pagesize = newpagesize
      this.List()
    },
    handleCurrentChange (newpage) {
      this.curpage = newpage
      this.List()
    },
    clickADD () {
      // 获取分类
      categories().then(res => {
        this.centerDialogVisible = true
        this.options = res.data.categories.map(items => {
          return items.cateName
        })
        console.log(this.options);
      })
    },
    // 添加分类
    clickADD_OK () {
      addcate(this.value, this.state).then(res => {
        if (res.data.code == 0) {
          this.$message({
            message: '添加成功!!!',
            type: 'success'
          });
          this.centerDialogVisible = false
        }
        this.List()
      })
    },
    // 获取 refreshTable
    List () {
      catelist(this.curpage, this.pagesize).then(res => {
        let dataarr = res.data.data;
        for (let i = 0; i < dataarr.length; i++) {
          dataarr[i].state = dataarr[i].state == 1;
          dataarr[i].isedit = false;
        }
        this.datalist = dataarr;
        this.total = res.data.total
      });
    }
  }
};
</script>

<style lang="less" scoped>
#TOP {
  display: flex;
  justify-content: space-between;
  align-items: center;
  button {
    margin-right: 50px;
  }
}
</style>